.support {
  margin-bottom: 200px;
}
.support__title {
  text-align: center;
  margin-bottom: 41px;
}
.registration {
  position: relative;
  padding-top: 45px;
}
.registration__buttons {
  margin-bottom: 41px;
}
.login__title {
  text-align: center;
}
.login__link {
  text-align: center;
  background: #f2f2f2;
  border-radius: 20px;
  display: inline-block;
  margin: 0 auto;
  padding: 10px 20px 10px 50px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  position: relative;
}
.login__link::after {
  content: "";
  position: absolute;
  border-radius: 50%;
  background-image: url(./images/icons/icons.svg#lock);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  top: 50%;
  left: 10px;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
  width: 20px;
  height: 20px;
  z-index: 1;
}

.dark .login__link {
  background: #1e1e1e;
}
.status__image {
  width: 98px;
  height: 98px;
}
.tarif__subtitle {
  margin-bottom: 47px;
}
.tarif__text {
  margin-bottom: 28px;
  padding-left: 60px;
  position: relative;
}
.tarif__card {
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 10px;
}
._no-webp .tarif__text_type_email::before {
  background-image: url(./images/mail.png);
}
.tarif__text_type_email::before {
  background-image: url(./images/mail.png);
}
.tarif__text_type_email::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  width: 50px;
  height: 50px;
}
._no-webp .tarif__text_type_browser::before {
