.filter {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-column-gap: calc(10px + 33 * ((100vw - 320px) / 1308));
  -moz-column-gap: calc(10px + 33 * ((100vw - 320px) / 1308));
  column-gap: calc(10px + 33 * ((100vw - 320px) / 1308));
  row-gap: 16px;
  margin-bottom: calc(16px + 71 * ((100vw - 320px) / 1308));
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
}
.filter__wrapper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-column-gap: calc(5px + 4 * ((100vw - 320px) / 1308));
  -moz-column-gap: calc(5px + 4 * ((100vw - 320px) / 1308));
  column-gap: calc(5px + 4 * ((100vw - 320px) / 1308));
}
.filter__subtitle {
  margin: 0;
  padding: 0;
  font-style: normal;
  font-weight: 700;
  font-size: calc(10px + 8 * ((100vw - 320px) / 1308));
  line-height: 100%;
  letter-spacing: 0.05em;
  color: #0e0e0e;
}
.filter__button {
  font-weight: 700;
  font-size: calc(10px + 8 * ((100vw - 320px) / 1308));
  line-height: 100%;
  letter-spacing: 0.05em;
  color: #5f398b;
  cursor: pointer;
}
.page-count {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: end;
  -ms-flex-pack: end;
  justify-content: flex-end;
  -webkit-column-gap: 31px;
  -moz-column-gap: 31px;
  column-gap: 31px;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
}
.page-count__wrapper {
  font-size: calc(10px + 14 * ((100vw - 320px) / 1308));
  line-height: 152.51%;
  letter-spacing: 0.05em;
  color: rgba(14, 14, 14, 0.4);
}
.page-count__arrow {
  -webkit-box-pack: end !important;
  -ms-flex-pack: end !important;
  justify-content: flex-end !important;
  -webkit-column-gap: 26px;
  -moz-column-gap: 26px;
  column-gap: 26px;
}

@media screen and (max-width: 1627.98px) {
  input[type="checkbox"].ios8-switch + label {
    padding-top: calc(6px + -6 * ((100vw - 320px) / 1308));
  }

