.header__burger span:nth-of-type(2),
.header__burger span:nth-of-type(3),
.header__burger_animate-1 span:nth-of-type(1) {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}
.header__burger span:nth-of-type(2):before,
.header__burger span:nth-of-type(3):after,
.header__burger span:nth-of-type(3):before {
  display: block;
  width: 20px;
  height: 2px;
  border-radius: 2px;
  background: -webkit-gradient(
    linear,
    left top,
    left bottom,
    from(#441e78),
    to(#633d8d)
  );
  background: -o-linear-gradient(top, #441e78 0, #633d8d 100%);
  background: linear-gradient(180deg, #441e78 0, #633d8d 100%);
}
