.header__burger_animate-1 span:nth-of-type(1) {
  display: block;
  width: 30px;
  height: 30px;
  border: 2px solid #633d8d;
  border-radius: 100%;
  -webkit-animation-name: circle;
  animation-name: circle;
  -webkit-animation-iteration-count: 1;
  animation-iteration-count: 1;
  -webkit-animation-direction: alternate;
  animation-direction: alternate;
}
.header__burger_animate-2 span:nth-of-type(1) {
  -webkit-animation-name: circle2;
  animation-name: circle2;
  -webkit-animation-iteration-count: 1;
  animation-iteration-count: 1;
  animation-direction: alternate-reverse;
}
.header__burger span:nth-of-type(2) {
  display: block;
  height: 30px;
  width: 30px;
  overflow: hidden;
  text-indent: -99999px;
}
.header__burger span:nth-of-type(2):before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -1px;
  margin-left: -10px;
}
.header__burger_animate-1 span:nth-of-type(2):before {
  -webkit-animation-iteration-count: 1;
  animation-iteration-count: 1;
  -webkit-animation-name: line-middle;
  animation-name: line-middle;
  animation-iteration-count: 1;
}
.header__burger_animate-2 span:nth-of-type(2):before {
  -webkit-animation-iteration-count: 1;
  animation-iteration-count: 1;
  -webkit-animation-name: line-middle2;
  animation-name: line-middle2;
  animation-iteration-count: 1;
  animation-direction: alternate-reverse;
}
.header__burger span:nth-of-type(3) {
  display: block;
  height: 30px;
  width: 30px;
  text-indent: -99999px;
}
.header__burger span:nth-of-type(3):after,
.header__burger span:nth-of-type(3):before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -10px;
}
.header__burger span:nth-of-type(3):before {
  margin-top: -7px;
}
.header__burger span:nth-of-type(3):after {
  margin-top: 5px;
}
.header__burger_animate-1 span:nth-of-type(3):before {
  -webkit-animation-name: line-top;
  animation-name: line-top;
  -webkit-animation-iteration-count: 1;
  animation-iteration-count: 1;
  -webkit-animation-direction: alternate;
  animation-direction: alternate;
}
.header__burger_animate-1 span:nth-of-type(3):after {
  -webkit-animation-name: line-bottom;
  animation-name: line-bottom;
